import {ManagedObject} from "@service/managed-object";
import {WitnessHostData} from "@generated/witness-host-data";
import {FaultDomainModel} from "@model/fault-domain.model";

export class EditDomainContext {
   prepopulateName: string = "";
   preselectHosts: string[] = [];
   witnessInfo: WitnessHostData;
   clusterRef: ManagedObject;

   static createDomain(
         preselectHosts: string[],
         witnessInfo: WitnessHostData,
         clusterRef: ManagedObject): EditDomainContext {
      return {
         prepopulateName: "",
         preselectHosts: preselectHosts,
         witnessInfo: witnessInfo,
         clusterRef: clusterRef,
      };
   }
   static editDomain(
         domain: FaultDomainModel,
         witnessInfo: WitnessHostData,
         clusterRef: ManagedObject): EditDomainContext {
      return {
         prepopulateName: domain.data.uid,
         preselectHosts: domain.hosts.map(h => h.data.uid),
         witnessInfo: witnessInfo,
         clusterRef: clusterRef,
      };
   }
}
