import {Component, EventEmitter, OnInit, Output} from "@angular/core";
import {VsanStretchedClusterMutationProvider} from "@generated/vsan-stretched-cluster-mutation-provider";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "disable-stretched-cluster.html",
   styleUrls: ["disable-stretched-cluster.scss"],
   selector: "disable-stretched-cluster-prompt",
})
export class DisableStretchedClusterComponent implements OnInit {
   clusterRef: ManagedObject = null;
   witnessHost: ManagedObject = null;
   witnessName: string = "";

   errors: string[] = null;
   busy: boolean = false;
   readonly dialogTitle: string = VsanUiUtils.getString("vsan.faultDomains.stretchedCluster.action.disable.title");

   @Output()
   private onError: EventEmitter<string> = new EventEmitter<string>();

   constructor(private mutationProvider: VsanStretchedClusterMutationProvider) {
   }

   ngOnInit(): void {
      const context: any = VsanUiUtils.getModalContext();
      this.clusterRef = context.clusterRef;
      this.witnessHost = context.witnessHost;
      this.witnessName = context.witnessName;
   }

   apply(): void {
      this.busy = true;
      this.mutationProvider.removeWitnessHost(this.clusterRef, this.witnessHost)
            .then((task: ManagedObject) => {
               VsanUiUtils.closeModalDialog(task);
            }).catch((error: string) => {
               this.errors = [error];
               this.busy = false;
            });
   }
}