import {Component, EventEmitter, Input, Output} from "@angular/core";
import {
   CreateDomainEvent,
   DeleteDomainEvent,
   EditDomainEvent,
   FaultDomainEvent,
   MoveHostsEvent,
   PromoteDomainEvent,
} from "../../common/event/fault-domain.event";
import {ClrSelectedState} from "@clr/angular";
import {FaultDomainModel} from "@model/fault-domain.model";

@Component({
   selector: "vsan-fault-domain-action-menu",
   templateUrl: "fault-domain-action-menu.html",
   styleUrls: ["fault-domain-action-menu.scss"],
})
export class FaultDomainActionMenuComponent {

   @Input()
   domain: FaultDomainModel;

   @Input()
   isStretchedCluster: boolean;

   @Input()
   iconId: string = "ellipsis-vertical";

   @Input()
   disabled: boolean;

   @Input()
   targetDomains: string[];

   @Output()
   onActionEvent: EventEmitter<FaultDomainEvent> = new EventEmitter<FaultDomainEvent>();

   get selectionCount(): number {
      return this.domain.hosts.filter(host => host.isSelected  === ClrSelectedState.SELECTED).length;
   }

   get moveActionDisabled(): boolean {
      // Disable hosts moving for a stretched cluster when:
      // 1) All the hosts within the fault domain are selected
      // 2) And the fault domain is preferred or there are <= 2 fault domains
      return this.isStretchedCluster && (this.selectionCount === this.domain.hosts.length) &&
            (this.domain.isPreferred || this.domain.validTargetDomains.length <= 1);
   }

   onPromote(): void {
      const event: PromoteDomainEvent = new PromoteDomainEvent();
      event.targetDomain = this.domain;
      this.onActionEvent.emit(event);
   }

   onEdit(): void {
      const event: EditDomainEvent = new EditDomainEvent();
      event.targetDomain = this.domain;
      this.onActionEvent.emit(event);
   }

   onRemove(): void {
      const event: DeleteDomainEvent = new DeleteDomainEvent();
      event.targetDomain = this.domain;
      this.onActionEvent.emit(event);
   }

   onCreate(): void {
      const event: CreateDomainEvent = new CreateDomainEvent();
      event.memberHosts = this.domain.hosts.filter(host => host.isSelected === ClrSelectedState.SELECTED);
      this.onActionEvent.emit(event);
   }

   onMove(targetDomain: string): void {
      const event: MoveHostsEvent = new MoveHostsEvent();
      event.hosts = this.domain.hosts.filter(host => host.isSelected === ClrSelectedState.SELECTED);
      event.targetDomain = targetDomain;
      this.onActionEvent.emit(event);
   }

}
