import {Component, OnInit, ViewChild} from "@angular/core";
import {AbstractControl, FormBuilder, FormControl, FormGroup, ValidationErrors, ValidatorFn} from "@angular/forms";
import {ClrForm} from "@clr/angular";
import {ConfigureVsanClusterMutationProvider} from "@generated/configure-vsan-cluster-mutation-provider";
import {ConnectionState} from "@generated/connection-state";
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {VsanFaultDomainSpec} from "@generated/vsan-fault-domain-spec";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ManagedObject, ManagedObjectUtils} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {MoveHostsContext} from "./move-hosts-context";

@Component({
   selector: "fault-domains-move-hosts",
   templateUrl: "move-hosts-confirmation.html",
   styleUrls: ["move-hosts-confirmation.scss"],
})
export class MoveHostsConfirmationComponent implements OnInit {

   context: MoveHostsContext;
   @ErrorMessage()
   alertMessage: string;
   @LoadingIndicator()
   busy: boolean;
   domainNameValidationError: string;

   domainSettings: FormGroup;

   @ViewChild(ClrForm, {static: true})
   domainSettingsForm: ClrForm;

   constructor(private configureClusterMutationProvider: ConfigureVsanClusterMutationProvider,
         private formBuilder: FormBuilder) {
      this.domainSettings = this.formBuilder.group({
         domainName: new FormControl("", [this.domainValidator]),
      });
   }

   ngOnInit(): void {
      this.context = VsanUiUtils.getModalContext();
      if (this.context.targetDomain) {
         this.domainSettings.setValue({domainName: this.context.targetDomain});
      }
      this.checkForInaccessibleHosts();
   }

   @Loader()
   async onMove(): Promise<void> {
      // The form has already been marked as touched by the vsan-auto-focus directive on the input.
      // The code, however, is left to ensure that behavior in case of directive removal.
      if (this.domainSettings.invalid) {
         this.domainSettingsForm.markAsTouched();
         return;
      }

      if (!this.checkForInaccessibleHosts()) {
         return;
      }
      const promises: Promise<ManagedObject>[] = this.context.hosts.map((host: DomainOrHostData) => {
         const hostRef: ManagedObject = ManagedObjectUtils.fromUid(host.uid);
         const spec: VsanFaultDomainSpec = VsanFaultDomainSpec.Factory.create(this.getDomainName(), hostRef);
         return this.configureClusterMutationProvider.updateFaultDomainInfo(hostRef, spec);
      });

      const tasks: ManagedObject[] = await Promise.all(promises);
      VsanUiUtils.closeModalDialog(tasks);
   }

   domainValidator: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
      this.domainNameValidationError = "";
      if (!this.context?.promptForDomainName) {
         // No domain name needed, nothing to validate
         return;
      }

      const domainName: string = this.getDomainName();
      if (!domainName) {
         this.domainNameValidationError = VsanUiUtils.getString(
               "vsan.faultDomains.action.createDomain.validation.noName");
      } else if (domainName.length > 256) {
         this.domainNameValidationError = VsanUiUtils.getString(
               "vsan.faultDomains.action.renameDomain.validation.nameTooLong");
      }
      if (this.domainNameValidationError) {
         return {"domainInvalid": {value: control.value}};
      }
      return null;
   };

   private getDomainName(): string {
      return this.domainSettings && this.domainSettings.get("domainName").value
            && this.domainSettings.get("domainName").value.trim();
   }

   private checkForInaccessibleHosts(): boolean {
      const inaccessibleHosts: DomainOrHostData[] = this.context.hosts.filter(
            host => host.connectionState !== ConnectionState.connected);
      if (!inaccessibleHosts?.length) {
         return true;
      }

      if (inaccessibleHosts.length === 1) {
         this.alertMessage =
               VsanUiUtils.getString(
                     "vsan.faultDomains.action.deleteDomain.validation.hostDisconnected",
                     inaccessibleHosts[0].label);
      } else {
         this.alertMessage =
               VsanUiUtils.getString(
                     "vsan.faultDomains.action.deleteDomain.validation.hostsDisconnected",
                     inaccessibleHosts.join(","));
      }
      return false;
   }

}
