import {Component, OnInit, ViewChild} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {Icon} from "@util/icon";
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {ConnectionState} from "@generated/connection-state";
import {VsanFaultDomainSpec} from "@generated/vsan-fault-domain-spec";
import {PreferredFaultDomainData} from "@generated/preferred-fault-domain-data";
import {VsanStretchedClusterMutationProvider} from "@generated/vsan-stretched-cluster-mutation-provider";
import {EditDomainContext} from "./edit-domain-context";
import {
   AbstractControl,
   FormBuilder,
   FormControl,
   FormGroup,
   ValidationErrors,
   ValidatorFn,
} from "@angular/forms";
import {ClrForm} from "@clr/angular";
import {VsanStretchedClusterService} from "@generated/vsan-stretched-cluster-service";
import {ConfigureVsanClusterMutationProvider} from "@generated/configure-vsan-cluster-mutation-provider";
import {FaultDomainModel} from "@model/fault-domain.model";

@Component({
   templateUrl: "edit-domain.html",
   styleUrls: ["edit-domain.scss"],
   selector: "vsan-fault-domains-edit-domain",
})
export class EditDomainComponent implements OnInit {

   readonly Icon: typeof Icon = Icon;
   private existingDomains: any = {};

   context: EditDomainContext;
   hosts: FaultDomainModel[] = [];
   selectedHosts: FaultDomainModel[] = [];
   loading: boolean = true;
   alertMessages: string[];
   domainNameValidationError: string;

   domainSettings: FormGroup;

   @ViewChild(ClrForm, {static: true})
   domainSettingsForm: ClrForm;

   constructor(private stretchedClusterService: VsanStretchedClusterService,
         private configureClusterMutationProvider: ConfigureVsanClusterMutationProvider,
         private stretchedClusterMutationProvider: VsanStretchedClusterMutationProvider,
         private formBuilder: FormBuilder) {
      this.domainSettings = this.formBuilder.group({
         domainName: new FormControl("", [this.domainValidator]),
      });
   }

   async ngOnInit(): Promise<void> {
      this.context = VsanUiUtils.getModalContext();
      if (this.context && this.context.prepopulateName) {
         this.domainSettings.setValue({domainName: this.context.prepopulateName});
      }

      try {
         const hostsData: DomainOrHostData[] = await this.stretchedClusterService.getAvailableDomains(
               ManagedObject.contextObject);
         this.handleData(hostsData);
         this.loading = false;
      } catch (error) {
         this.loading = false;
         this.alertMessages = [error];
      }
   }

   handleData(data: DomainOrHostData[]): void {
      const hosts: FaultDomainModel[] = data
            .filter(item => item.isHost)
            .map(item => FaultDomainModel.createHost(item, null, []));
      const domains: FaultDomainModel[] = data
            .filter(item => !item.isHost)
            .map(item => FaultDomainModel.createDomain(item, []));
      domains.forEach(domain => this.existingDomains[domain.data.uid] = domain);
      domains.forEach(domain => hosts.push(...domain.hosts));

      for (const host of hosts) {
         if (this.context.preselectHosts.indexOf(host.data.uid) !== -1) {
            this.selectedHosts.push(host);
         }
      }
      this.hosts = hosts;
   }

   onConfirm(): void {
      // The form has already been marked as touched by the vsan-auto-focus directive on the input.
      // The code, however, is left to ensure that behavior in case of directive removal.
      if (this.domainSettings.invalid) {
         this.domainSettingsForm.markAsTouched();
         return;
      }

      this.alertMessages = this.validateHosts();
      if (!!this.alertMessages && this.alertMessages.length > 0) {
         return;
      }

      const domainName: string = this.getDomainName();

      const specs: VsanFaultDomainSpec[] = this.selectedHosts
            .filter(host => !host.domain || host.domain.data.uid !== domainName)
            .map(host => VsanFaultDomainSpec.Factory.create(
                  domainName, ManagedObject.fromUid(host.data.uid)));

      if (this.context.prepopulateName.length > 0) {
         // renaming existing domain
         const domain: FaultDomainModel = this.existingDomains[this.context.prepopulateName];
         const unchecked: FaultDomainModel[] = domain.hosts.filter(host => this.selectedHosts.indexOf(host) === -1);
         unchecked.forEach(host => specs.push(
               VsanFaultDomainSpec.Factory.create(null, ManagedObject.fromUid(host.data.uid))));
      }

      const promises: Promise<ManagedObject>[] = specs
            .map(spec => this.configureClusterMutationProvider.updateFaultDomainInfo(spec.hostRef, spec));

      if (promises.length === 0) {
         VsanUiUtils.closeModalDialog([]);
         return;
      }
      // Call setPreferredFaultDomain() when you rename a preferred domain from a stretched cluster
      if (this.context.witnessInfo) {
         const isCurrentDomainPreferred: boolean =
               this.context.witnessInfo.preferredFaultDomainName === this.context.prepopulateName;
         const isCurrentDomainRenamed: boolean = domainName !== this.context.prepopulateName;
         if (isCurrentDomainPreferred && isCurrentDomainRenamed) {
            const spec: PreferredFaultDomainData = PreferredFaultDomainData.Factory.create(domainName);
            promises.push(this.stretchedClusterMutationProvider.setPreferredFaultDomain(this.context.clusterRef, spec));
         }
      }

      this.loading = true;
      Promise.all(promises)
            .then((tasks: ManagedObject[]) => VsanUiUtils.closeModalDialog(tasks))
            .catch(() => VsanUiUtils.closeModalDialog([]));
   }

   validateHosts(): string[] {
      if (this.selectedHosts.length === 0) {
         return [VsanUiUtils.getString("vsan.faultDomains.action.createDomain.validation.noHost")];
      }

      const disconnectedHostWarning: string[] = this.selectedHosts
            .filter(host => host.data.connectionState !== ConnectionState.connected)
            .map(host => VsanUiUtils.getString(
                  "vsan.faultDomains.action.createDomain.validation.disconnectedHost",
                  host.data.label));
      if (disconnectedHostWarning.length > 0) {
         return disconnectedHostWarning;
      }

      if (this.context.witnessInfo) {
         const stretchedClusterConfigurationWarning: string = this.validateStretchedClusterConfiguration();
         if (stretchedClusterConfigurationWarning) {
            return [stretchedClusterConfigurationWarning];
         }
      }
   }

   domainValidator: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
      this.domainNameValidationError = "";
      const domainName: string = this.getDomainName();
      if (!domainName) {
         this.domainNameValidationError = VsanUiUtils.getString(
               "vsan.faultDomains.action.createDomain.validation.noName");
      } else if (domainName.length > 256) {
         this.domainNameValidationError = VsanUiUtils.getString(
               "vsan.faultDomains.action.renameDomain.validation.nameTooLong");
      } else if (this.existingDomains[domainName] && this.context.prepopulateName !== domainName) {
         this.domainNameValidationError = VsanUiUtils.getString(
               "vsan.faultDomains.action.createDomain.validation.duplicateName");
      }
      if (this.domainNameValidationError) {
         return {"domainInvalid": {value: control.value}};
      }
      return null;
   };

   getDomainName(): string {
      return this.domainSettings && this.domainSettings.get("domainName").value
            && this.domainSettings.get("domainName").value.trim();
   }

   private validateStretchedClusterConfiguration(): string {
      if (this.validateStretchedClusterStandaloneHostsNotAdded()) {
         return VsanUiUtils.getString("vsan.faultDomains.stretchedCluster.action.moveHosts.validation.moveToStandalone");
      }
      if (this.validateStretchedClusterPreferredDomainNotRemoved()) {
         return VsanUiUtils.getString("vsan.faultDomains.stretchedCluster.action.moveHosts.validation.removePreferred");
      }
      if (this.validateStretchedClusterRequiredDomainsNotRemoved()) {
         return VsanUiUtils.getString("vsan.faultDomains.stretchedCluster.action.moveHosts.validation.insufficientFDs");
      }
   }

   private validateStretchedClusterStandaloneHostsNotAdded(): boolean {
      // Validate not pulling out hosts from an existing domain to standalone via edit action
      const currentDomain: FaultDomainModel = this.existingDomains[this.context.prepopulateName];
      if (!currentDomain) {
         return false;
      }
      const selectedHostsFromCurrentDomainCount: number = this.selectedHosts.filter(
            host => host.domain && host.domain.data.uid === this.context.prepopulateName).length;
      return (currentDomain.hosts.length !== selectedHostsFromCurrentDomainCount);
   }

   private validateStretchedClusterPreferredDomainNotRemoved(): boolean {
      // Validate all hosts of a preferred domain are not removed
      const preferredDomainName: string = this.context.witnessInfo.preferredFaultDomainName;
      if (!(preferredDomainName && preferredDomainName !== this.context.prepopulateName)) {
         return false;
      }
      const preferredDomain: FaultDomainModel = this.existingDomains[preferredDomainName];
      const selectedHostsFromPreferredDomainCount: number = this.selectedHosts.reduce(
            (sum, host) => sum += (host.domain && host.domain.data.uid === preferredDomainName) ? 1 : 0, 0);
      return (preferredDomain && preferredDomain.hosts.length === selectedHostsFromPreferredDomainCount);
   }

   private validateStretchedClusterRequiredDomainsNotRemoved(): boolean {
      // Validate FDs are not less than 2 when moving hosts to particular FD
      const hostsCountFromDomainEntries: IterableIterator<[string, number]> = this.countSelectedHostsFromOtherDomains().entries();
      let domainsToRemove: number = 0;
      while (true) {
         const domainHostsCount: IteratorResult<[string, number]> = hostsCountFromDomainEntries.next();
         if (!domainHostsCount.done) {
            const domainName: string = domainHostsCount.value[0];
            const hostsCount: number = domainHostsCount.value[1];
            const domainModel: FaultDomainModel = this.existingDomains[domainName];
            if (domainModel.hosts.length === hostsCount) {
               domainsToRemove++;
            }
         } else {
            break;
         }
      }
      return (domainsToRemove && ((Object.keys(this.existingDomains).length - domainsToRemove) < 2));
   }

   private countSelectedHostsFromOtherDomains(): Map<string, number>  {
      const currentDomain: FaultDomainModel = this.existingDomains[this.context.prepopulateName];
      const selectedHostsCountFromDomain: Map<string, number> = new Map();
      this.selectedHosts.forEach(host => {
         // Count hosts that reside in other domains /exclude the current domain/
         const isHostFromDifferentDomain: boolean =
               host.domain && (!currentDomain || (host.domain.data.uid !== this.context.prepopulateName));
         if (isHostFromDifferentDomain) {
            let domainHostsCount: number = 1;
            if (selectedHostsCountFromDomain.has(host.domain.data.uid)) {
               domainHostsCount += selectedHostsCountFromDomain.get(host.domain.data.uid);
            }
            selectedHostsCountFromDomain.set(host.domain.data.uid, domainHostsCount);
         }
      });
      return selectedHostsCountFromDomain;
   }

   isHostEnabled(host: FaultDomainModel): boolean {
      return ConnectionState.connected === host.data.connectionState;
   }

   get title(): string {
      return this.context.prepopulateName.length > 0
            ? VsanUiUtils.getString("vsan.faultDomains.action.renameDomain.title")
            : VsanUiUtils.getString("vsan.faultDomains.action.createDomain.title");
   }

   get objectName(): string {
      return this.context.prepopulateName.length > 0 ? this.context.prepopulateName : null;
   }
}
