import {Component, EventEmitter, Input, Output} from "@angular/core";
import {ClrSelectedState} from "@clr/angular";
import {FaultDomainModel} from "@model/fault-domain.model";
import {CreateDomainEvent, FaultDomainEvent, MoveHostsEvent} from "../../common/event/fault-domain.event";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   selector: "standalone-hosts-action-menu",
   templateUrl: "standalone-hosts-action-menu.html",
   styleUrls: ["fault-domain-action-menu.scss"],
})
export class StandaloneHostsActionMenuComponent {

   @Input()
   standaloneHosts: FaultDomainModel[];

   @Input()
   targetDomains: FaultDomainModel[];

   @Input()
   disabled: boolean = false;

   @Input()
   disabledReason: string;

   @Input()
   isStretchedClusterEnabled: boolean = false;

   @Output()
   onActionEvent: EventEmitter<FaultDomainEvent> = new EventEmitter<FaultDomainEvent>();

   onCreate() {
      const createDomainEvent = new CreateDomainEvent();
      createDomainEvent.memberHosts = this.selection;
      this.onActionEvent.emit(createDomainEvent);
   }

   onMove(targetDomain: string) {
      const moveHostsEvent = new MoveHostsEvent();
      moveHostsEvent.hosts = this.selection;
      moveHostsEvent.targetDomain = targetDomain;
      this.onActionEvent.emit(moveHostsEvent);
   }

   get selection(): FaultDomainModel[] {
      return this.standaloneHosts.filter(host => host.isSelected === ClrSelectedState.SELECTED);
   }

   get dropdownLabel(): string {
      if (VsanUiUtils.isEmpty(this.selection)) {
         return "";
      }
      const labelKey: string = this.selection.length === 1 ?
            "vsan.faultDomains.action.hostCountOne" : "vsan.faultDomains.action.hostCount";
      return VsanUiUtils.getString(labelKey, this.selection.length);
   }
}
