import {Component, OnInit} from "@angular/core";
import {ClrSelectedState} from "@clr/angular";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {RefreshHandler} from "@service/global-refresh.service";
import {ModalBuilder} from "@util/modal-builder";
import {PermissionService} from "@generated/permission-service";
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {WitnessHostData} from "@generated/witness-host-data";
import {ConnectionState} from "@generated/connection-state";
import {
   ChangeWitnessHostEvent,
   ConfigureStretchedClusterEvent,
   CreateDomainEvent,
   DeleteDomainEvent,
   DisabledStretchedClusterEvent,
   EditDomainEvent,
   FaultDomainEvent,
   MoveHostsEvent,
   PromoteDomainEvent,
} from "../common/event/fault-domain.event";
import {Logger} from "@util/logger";
import {MoveHostsContext} from "./actions/move/move-hosts-context";
import {Privileges} from "@generated/privileges";
import {EditDomainContext} from "./actions/edit/edit-domain-context";
import {FaultDomainConsumptionData} from "@model/fault-domain-consumption-data";
import {TaskMonitorService} from "@service/task-monitor-service";
import {ChangeWitnessContext} from "@navigation/context/change-witness-context";
import {VsanStretchedClusterService} from "@generated/vsan-stretched-cluster-service";
import {CapacityDataService} from "@generated/capacity-data-service";
import {ScreenReaderAnnouncer} from "@service/screen-reader-announcer.service";
import {AdvancedOptionsService} from "@generated/advanced-options-service";
import {AdvancedOptionsInfo} from "@generated/advanced-options-info";
import {CapacityReservationUtil} from "@util/capacity-reservation-util";
import {CapacityReservationConfig} from "@generated/capacity-reservation-config";
import {HostStorageConsumptionData} from "@generated/host-storage-consumption-data";
import {UnavailableViewSpec} from "@component/unavailable-view/unavailable-view-spec";
import {VsanRdmaService} from "@generated/vsan-rdma-service";
import {VsanRoutes} from "../../vsan-routes";
import {FaultDomainModel} from "@model/fault-domain.model";
import {InlineViewContextKey} from "@model/inline-view-context-key.data";
import {PersistentStorageService} from "@service/browser-storage.service";
import {FaultDomainsUtil} from "@util/fault-domains-util";
import {VsanConfigService} from "@generated/vsan-config-service";

@Component({
   templateUrl: "fault-domains.html",
   styleUrls: ["fault-domains.scss"],
   selector: "vsan-fault-domains",
})
export class FaultDomainsComponent implements OnInit {

   readonly FaultDomainModel: typeof FaultDomainModel = FaultDomainModel;
   readonly FaultDomainsComponent: typeof FaultDomainsComponent = FaultDomainsComponent;
   private static readonly STRETCHED_CLUSTER_MIN_HOSTS_COUNT: number = 2;

   clusterRef: ManagedObject;
   faultDomainModels: FaultDomainModel[] = [];
   standaloneModels: FaultDomainModel[] = [];
   preferredDomain: string;
   secondaryDomain: string;
   data: DomainOrHostData[];
   storageConsumption: any = {};
   isVsanMaxEnabled: boolean;
   alertMessage: string = null;
   witnessInfos: WitnessHostData[] = null; // will be null if stretched cluster/metadata only node is disabled
   hasEditPermission: boolean = true;
   allHostsCount: number = 0;
   private hasDisconnectedHosts: boolean = false;
   disableCfgStretchedClusterBtn: boolean = false;
   showSpinner: boolean;
   isStretchedClusterSupported: boolean = false;
   isHostReservedCapacitySupported: boolean = false;
   isRdmaSupported: boolean = false;
   isRdmaStretchedClusterSupported: boolean = false;
   isSharedWitnessAllowed: boolean = false;
   isCapacityReservationEnabled: boolean = false;
   isRdmaEnabled: boolean = false;
   noHostsView: UnavailableViewSpec;
   faultToleranceDescription: string;

   @ScreenReaderAnnouncer()
   srOnlyMessage: string;

   constructor(
         private taskSvc: TaskMonitorService,
         private permissionService: PermissionService,
         private capabilityProvider: VsanCapabilityProvider,
         private vsanConfigService: VsanConfigService,
         private stretchedClusterService: VsanStretchedClusterService,
         private capacityDataService: CapacityDataService,
         private advancedOptionsService: AdvancedOptionsService,
         private storageService: PersistentStorageService,
         private rdmaService: VsanRdmaService) {
   }

   ngOnInit(): void {
      this.clusterRef = this.getClusterRefFromLocalStorage() || ManagedObject.contextObject;
      this.fetch();
   }

   @RefreshHandler
   fetch(): void {
      this.initializeLoading();

      const promises: Promise<any>[] = [];

      promises.push(this.vsanConfigService.getVsanConfigSpec(this.clusterRef)
            .then(config => this.isVsanMaxEnabled = config.isVsanMaxEnabled));

      promises.push(this.stretchedClusterService.getAvailableDomains(this.clusterRef)
            .then(domainsData => this.data = domainsData));

      promises.push(this.stretchedClusterService.getWitnessHostData(this.clusterRef)
            .then(witnessData => this.witnessInfos = witnessData));

      promises.push(this.capacityDataService.getStorageConsumptionByHost(this.clusterRef)
            .then(hostsCapacity => this.processHostsCapacity(hostsCapacity)));

      promises.push(this.advancedOptionsService.getAdvancedOptionsInfo(this.clusterRef)
            .then(advancedOptions => this.processAdvancedOptions(advancedOptions)));

      promises.push(this.rdmaService.isRdmaEnabled(this.clusterRef)
            .then(isRdmaEnabled => this.isRdmaEnabled = isRdmaEnabled));

      const manageFaultDomainPermissions: string[] = [Privileges.EDIT_CLUSTER(), Privileges.CONFIG_STORAGE()];
      promises.push(this.permissionService.hasPermissions(this.clusterRef, manageFaultDomainPermissions)
            .then((result: boolean) => this.hasEditPermission = result)
            .catch(() => this.hasEditPermission = true));

      promises.push(this.capabilityProvider.getClusterCapabilityData(this.clusterRef)
            .then(capability => {
               this.isStretchedClusterSupported = capability.isStretchedClusterSupported;
               this.isHostReservedCapacitySupported = capability.isHostReservedCapacitySupported;
               this.isRdmaSupported = capability.isRdmaSupported;
               this.isRdmaStretchedClusterSupported = capability.isRdmaStretchedClusterSupported;
            }));

      Promise.all(promises)
            .then(async () => {
               this.allHostsCount = this.data.reduce((sum, item) => sum += item.isHost ? 1 : item.children.length, 0);
               if (this.allHostsCount === 0) {
                  this.noHostsView = UnavailableViewSpec.FAULT_DOMAINS_VIEW_NO_HOSTS;
                  return;
               }
               this.isSharedWitnessAllowed = await this.getSharedWitnessAllowed();
               this.refresh(this.data, this.witnessInfos);
            })
            .catch((e) => {
               this.alertMessage = e;
               Logger.warn("Failed to get Fault Domains data!", e);
            })
            .finally(() => {
               this.showSpinner = false;
            });
   }

   private getClusterRefFromLocalStorage(): ManagedObject {
      return this.storageService.pop(InlineViewContextKey.FAULT_DOMAINS_CONTEXT_KEY);
   }

   private processHostsCapacity(hostsStorageData: HostStorageConsumptionData[]): void {
      this.storageConsumption = [];
      hostsStorageData.forEach(item => {
         if (item.hostRef) {
            this.storageConsumption[ManagedObject.uid(item.hostRef)] = item;
         }
      });
   }

   private processAdvancedOptions(advancedOptions: AdvancedOptionsInfo): void {
      if (!advancedOptions || !advancedOptions.capacityReservationConfig) {
         this.isCapacityReservationEnabled = false;
         return;
      }
      const reservationConfig: CapacityReservationConfig = advancedOptions.capacityReservationConfig;
      this.isCapacityReservationEnabled =
            CapacityReservationUtil.isReservationEnforced(reservationConfig.hostFailureReservation) ||
            CapacityReservationUtil.isReservationEnforced(reservationConfig.vsanOperationReservation);
   }

   private async getSharedWitnessAllowed(): Promise<boolean>  {
      if (this.isStretchedCluster && this.allHostsCount === 2) {
         const hostRef: ManagedObject = this.witnessInfos[0].witnessHost;
         let isSharedWitnessSupported: boolean;
         let isSharedWitnessSupportedOnVc: boolean;
         try {
            [isSharedWitnessSupported, isSharedWitnessSupportedOnVc] = await Promise.all([
               this.capabilityProvider.getIsSharedWitnessSupportedOnVc(hostRef),
               this.capabilityProvider.getIsSharedWitnessSupported(hostRef)]);
         } catch (e) {
            Logger.warn("Failed to fetch shared witness capability!", e);
            return false;
         }
         return isSharedWitnessSupported && isSharedWitnessSupportedOnVc;
      } else {
         return false;
      }
   }

   private refresh(data: DomainOrHostData[], witnessInfos: WitnessHostData[]): void {
      if (!this.data) {
         this.faultDomainModels.forEach(model => model.isLoading = true);
         return;
      }

      data.sort((item1, item2) => item1.label.localeCompare(item2.label));

      // Preferred domain name should be the same for all witness hosts, retrieve it from the first.
      const preferredDomain: string = FaultDomainsUtil.getPreferredDomain(witnessInfos);
      if (preferredDomain) {
         this.preferredDomain = preferredDomain;
         const domain: DomainOrHostData = data.find(model => !model.isHost && model.uid !== preferredDomain);
         this.secondaryDomain = domain ? domain.uid : null;
      } else {
         this.preferredDomain = null;
         this.secondaryDomain = null;
      }

      this.faultDomainModels = FaultDomainsUtil.getFaultDomainModels(data, preferredDomain);
      this.standaloneModels = FaultDomainsUtil.getStandaloneModels(data, witnessInfos);
      this.hasDisconnectedHosts =
            this.hasAnyDisconnectedState(data.filter(item => item.isHost), this.faultDomainModels);

      this.disableCfgStretchedClusterBtn =
            this.hasDisconnectedHosts
            || !this.isStretchedClusterSupported
            || !this.hasEnoughHostsForStretchedCluster()
            || this.isCapacityReservationEnabled
            || (this.isRdmaSupported && this.isRdmaEnabled && !this.isRdmaStretchedClusterSupported);
      this.srOnlyMessage = VsanUiUtils.getString("vsan.screenReader.message.page.loaded",
            VsanUiUtils.getString("vsan.faultDomains"));
      this.faultToleranceDescription = FaultDomainsUtil.getFaultToleranceInfo(data,
            witnessInfos,
            this.standaloneModels,
            this.faultDomainModels,
            this.storageConsumption);
   }

   private hasAnyDisconnectedState(standaloneHosts: DomainOrHostData[],
         faultDomainModels: FaultDomainModel[]): boolean {
      let result: boolean = this.checkDisconnectedState(standaloneHosts);
      if (!result) {
         result = faultDomainModels.some(item => this.checkDisconnectedState(item.data.children));
      }
      return result;
   }

   private checkDisconnectedState = (hosts: any[]): boolean =>
         hosts.some(item => item.connectionState !== ConnectionState.connected);

   private hasEnoughHostsForStretchedCluster = (): boolean => {
      return this.allHostsCount >= FaultDomainsComponent.STRETCHED_CLUSTER_MIN_HOSTS_COUNT;
   };

   get stretchedClusterDisabledReason(): string {
      if (this.disableCfgStretchedClusterBtn) {
         if (!this.isStretchedClusterSupported) {
            return VsanUiUtils.getString("vsan.faultDomains.label.notSupported");
         } else if (this.hasDisconnectedHosts) {
            return VsanUiUtils.getString("vsan.faultDomains.label.hostDisconnected");
         } else if (!this.hasEnoughHostsForStretchedCluster()) {
            return VsanUiUtils.getString("vsan.faultDomains.label.minHostsRquirement");
         } else if (this.isCapacityReservationEnabled) {
            return VsanUiUtils.getString("vsan.faultDomains.label.capacityReservationEnabled");
         } else if (this.isRdmaSupported && this.isRdmaEnabled && !this.isRdmaStretchedClusterSupported) {
            return VsanUiUtils.getString("vsan.faultDomains.label.rdmaEnabled");
         }
      }
      return "";
   }

   get faultDomainActionsDisbaledReason(): string {
      if (this.isCapacityReservationEnabled) {
         return VsanUiUtils.getString("vsan.faultDomains.label.capacityReservationEnabled");
      } else if (!this.hasEditPermission) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      }
      return "";
   }

   get disableChangeWitnessHostBtnReason(): string {
      if (!this.hasEditPermission) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      } else if (this.hasDisconnectedHosts) {
         return VsanUiUtils.getString("vsan.faultDomains.action.changeHost.disable.label");
      } else if (!this.isStretchedClusterSupported) {
         return VsanUiUtils.getString("vsan.faultDomains.label.notSupported");
      } else if (this.allHostsCount < FaultDomainsComponent.STRETCHED_CLUSTER_MIN_HOSTS_COUNT) {
         return VsanUiUtils.getString("vsan.faultDomains.label.minHostsRquirement");
      }
      return "";
   }

   getDomainConsumption(model: FaultDomainModel): FaultDomainConsumptionData {
      return FaultDomainConsumptionData.getDomainConsumptionData(model, this.storageConsumption);
   }

   handleActionEvent(event: FaultDomainEvent): void {
      if (event instanceof CreateDomainEvent) {
         this.onCreateDomain(event.memberHosts);
      } else if (event instanceof EditDomainEvent) {
         this.onEditDomain(event.targetDomain);
      } else if (event instanceof DeleteDomainEvent) {
         this.onDeleteDomain(event.targetDomain);
      } else if (event instanceof MoveHostsEvent) {
         this.onMoveRequest(event.hosts, event.targetDomain);
      } else if (event instanceof PromoteDomainEvent) {
         this.onMarkAsPreferred(event.targetDomain);
      } else if (event instanceof ConfigureStretchedClusterEvent) {
         this.onConfigureStretchedCluster();
      } else if (event instanceof DisabledStretchedClusterEvent) {
         this.onDisableStretchedCluster();
      } else if (event instanceof ChangeWitnessHostEvent) {
         this.onChangeWitness();
      } else {
         Logger.warn("unknown action event", event);
      }
   }

   onMoveRequest(affectedHosts: FaultDomainModel[], targetDomain: string): void {
      if (affectedHosts.length === 0) {
         return;
      }
      // Prevent invalid stretched cluster configuration:
      if (this.isStretchedCluster) {
         // 1) Don't move host from a domain to standalone hosts
         const isStandaloneHostTarget: boolean = !targetDomain;
         if (isStandaloneHostTarget) {
            return;
         }
         // 2) Don't move all hosts from domain 'A' to domain 'B' if domain 'A' is preferred or all domains are =< 2
         const isDomainPreferred: boolean = affectedHosts[0].domain && affectedHosts[0].domain.isPreferred;

         if (FaultDomainModel.allAffected(affectedHosts) && (this.faultDomainModels.length <= 2 || isDomainPreferred)) {
            return;
         }
      }

      const actionContext: MoveHostsContext = MoveHostsContext.moveHosts(affectedHosts, targetDomain);

      new ModalBuilder("faultDomains/moveHosts")
            .setSize(640, actionContext.warnDestructiveForSourceDomain ? 260 : 220)
            .open(actionContext)
            .then(taskRefs => this.handleTaskList(taskRefs.map(ManagedObject.uid)));
   }

   onCreateDomain(affectedHosts: FaultDomainModel[]): void {
      const context: MoveHostsContext = MoveHostsContext.createDomain(affectedHosts);
      new ModalBuilder("faultDomains/moveHosts")
            .setSize(640, context.warnDestructiveForSourceDomain ? 320 : 280)
            .open(context)
            .then(taskRefs => this.handleTaskList(taskRefs.map(ManagedObject.uid)));
   }

   onEditDomain(domain: FaultDomainModel = null): void {
      let context: EditDomainContext;
      // pass witnessInfo for stretched cluster case only
      const witnessInfo: WitnessHostData = this.isStretchedCluster ? this.witnessInfos[0] : null;
      if (domain) {
         context = EditDomainContext.editDomain(domain, witnessInfo, this.clusterRef);
      } else {
         const allSelectedHosts: any[] = this.selectedStandaloneHosts.map(host => host.data.uid);
         for (const domainModel of this.faultDomainModels) {
            allSelectedHosts.push(...domainModel.hosts
                  .filter(host => host.isSelected === ClrSelectedState.SELECTED)
                  .map(host => host.data.uid));
         }
         context = EditDomainContext.createDomain(allSelectedHosts, witnessInfo, this.clusterRef);
      }

      new ModalBuilder("faultDomains/editDomain")
            .setSize(650, 540)
            .open(context)
            .then(taskRefs => this.handleTaskList(taskRefs.map(ManagedObject.uid)));
   }

   onDeleteDomain(domain: FaultDomainModel): void {
      new ModalBuilder("faultDomains/moveHosts")
            .setSize(640, 260)
            .open(MoveHostsContext.deleteDomain(domain))
            .then(taskRefs => this.handleTaskList(taskRefs.map(ManagedObject.uid)));
   }

   onConfigureStretchedCluster(): void {
      new ModalBuilder(VsanRoutes.CONFIGURE_STRETCHED_CLUSTER_WIZARD)
            .setDimensions(ModalBuilder.WIZARD_XL)
            .open()
            .then((taskRef) => this.handleTaskList([ManagedObject.uid(taskRef)]));
   }

   onDisableStretchedCluster(): void {
      const context: any = {
         clusterRef: this.clusterRef,
         witnessHost: this.witnessInfos[0].witnessHost,
         witnessName: this.witnessInfos[0].witnessHostName,
      };
      new ModalBuilder("faultDomains/disableStretchedCluster")
            .setSize(640, 220)
            .open(context)
            .then((taskRef) => this.handleTaskList([ManagedObject.uid(taskRef)]));
   }

   onMarkAsPreferred(domain: FaultDomainModel): void {
      const context: any = {
         clusterRef: this.clusterRef,
         domainName: domain.data.uid,
      };
      new ModalBuilder("faultDomains/promoteDomain")
            .setSize(640, 380)
            .open(context)
            .then((taskRefs) => this.handleTaskList([ManagedObject.uid(taskRefs[0])]));
   }

   onChangeWitness(): void {
      const context: ChangeWitnessContext = {
         preferredDomain: this.preferredDomain,
         currentWitnessHost: this.witnessInfos[0].witnessHost,
      };
      new ModalBuilder(VsanRoutes.CONFIGURE_STRETCHED_CLUSTER_WIZARD)
            .setDimensions(ModalBuilder.WIZARD_XL)
            .open(context)
            .then((taskRef) => this.handleTaskList([ManagedObject.uid(taskRef)]));
   }

   onDropInPlaceholder(event: any, intoStandalone: boolean): void {
      const hosts: FaultDomainModel[] = [...event.dragData.selection];
      if (!hosts.find(model => model.data.uid === event.dragData.host.data.uid)) {
         hosts.push(event.dragData.host);
      }
      hosts.forEach(host => host.isDragging = false);
      if (intoStandalone) {
         // For stretched cluster don't move hosts from a domain to standalone hosts
         if (this.isStretchedCluster) {
            return;
         }
         this.onMoveRequest(hosts, null);
      } else {
         // For stretched cluster don't move all hosts from a preferred domain
         const allHostsAreAffected: boolean = FaultDomainModel.allAffected(hosts);
         const isDomainPreferred: boolean = hosts[0].domain && hosts[0].domain.isPreferred;
         if (this.isStretchedCluster && allHostsAreAffected && isDomainPreferred) {
            return;
         }

         this.onCreateDomain(hosts);
      }
   }

   get isStretchedCluster(): boolean {
      return !!this.witnessInfos && this.witnessInfos.length === 1 && !this.witnessInfos[0].isMetadataWitnessHost;
   }

   isCreateFaultDomainAllowed(): boolean {
      return (!this.isStretchedCluster || this.faultDomainModels.length < 2) &&
            !this.isCapacityReservationEnabled;
   }

   get validTargetsForStandaloneHosts(): string[] {
      if (this.isCapacityReservationEnabled) {
         // Prevent the drag and drop action when capacity reserve is enabled
         return [];
      }
      const result: string[] = [FaultDomainModel.CREATE_DOMAIN_PLACEHOLDER];
      if (this.data) {
         this.data.filter(item => !item.isHost)
               .map(item => item.uid)
               .forEach(item => result.push(item));
      }
      return result;
   }

   get selectedStandaloneHosts(): FaultDomainModel[] {
      return this.standaloneModels.filter(model => model.isSelected === ClrSelectedState.SELECTED);
   }

   get hasDomains(): boolean {
      return this.faultDomainModels.length === 0;
   }

   /**
    * handles the list of task UIDs returned by various action dialogs.
    * waits for the task results and refreshes the view.
    * @param taskUids
    */
   private handleTaskList = (taskUids: string[]) => {
      this.initializeLoading();
      if (taskUids === null || taskUids.length === 0) {
         this.fetch();
         return;
      }

      const promises: Promise<any>[] = [];
      taskUids.forEach(t => promises.push(this.taskSvc.getAwaitResult(ManagedObject.fromUid(t))));
      Promise.all(promises).then(() => this.fetch()).catch(() => this.fetch());
   };

   private initializeLoading = () => {
      this.data = null;
      this.faultDomainModels = [];
      this.standaloneModels = [];
      this.showSpinner = true;
      this.noHostsView = null;
   };
}
