import {NgModule} from "@angular/core";
import {CommonModule} from "@angular/common";
import {FormsModule, ReactiveFormsModule} from "@angular/forms";
import {RouterModule, Routes} from "@angular/router";
import {ClarityModule} from "@clr/angular";
import {DiskmgmtCommonModule} from "@component/diskmgmt/diskmgmt-common.module";
import {VsanCommonModule} from "app/vsan/common/vsan-common.module";
import {FaultDomainsComponent} from "./fault-domains.component";
import {DisableStretchedClusterComponent} from "./actions/stretchedcluster/disable-stretched-cluster.component";
import {PromoteDomainComponent} from "./actions/stretchedcluster/promote-domain.component";
import {EditDomainComponent} from "./actions/edit/edit-domain.component";
import {MoveHostsConfirmationComponent} from "./actions/move/move-hosts-confirmation.component";
import {StandaloneHostsActionMenuComponent} from "./view/standalone-hosts-action-menu.component";
import {CategoryLabelComponent} from "./component/category-label/category-label.component";
import {FaultDomainActionMenuComponent} from "./view/fault-domain-action-menu.component";
import {NgDragDropModule} from "ng-drag-drop";
import {UnavailableViewModule} from "@component/unavailable-view/unavailable-view.module";
import {ConfigurationsModule} from "@component/configurations/configurations.module";
import {PersistentStorageService} from "@service/browser-storage.service";

const routes: Routes = [
   {
      path: "",
      component: FaultDomainsComponent,
   },
   {
      path: "disableStretchedCluster",
      component: DisableStretchedClusterComponent,
   },
   {
      path: "promoteDomain",
      component: PromoteDomainComponent,
   },
   {
      path: "editDomain",
      component: EditDomainComponent,
   },
   {
      path: "moveHosts",
      component: MoveHostsConfirmationComponent,
   },
];

@NgModule({
   imports: [
      CommonModule,
      FormsModule,
      ReactiveFormsModule,
      ClarityModule,
      VsanCommonModule,
      ConfigurationsModule,
      DiskmgmtCommonModule,
      UnavailableViewModule,
      RouterModule.forChild(routes),
      NgDragDropModule.forRoot(),
   ],
   declarations: [
      FaultDomainsComponent,
      CategoryLabelComponent,
      DisableStretchedClusterComponent,
      PromoteDomainComponent,
      EditDomainComponent,
      MoveHostsConfirmationComponent,
      StandaloneHostsActionMenuComponent,
      FaultDomainActionMenuComponent,
   ],
   providers: [
      PersistentStorageService,
   ],
})
export class FaultDomainsModule {
}
