import {Component, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {PreferredFaultDomainData} from "@generated/preferred-fault-domain-data";
import {VsanStretchedClusterMutationProvider} from "@generated/vsan-stretched-cluster-mutation-provider";

@Component({
   templateUrl: 'promote-domain.html',
   styleUrls: ['promote-domain.scss'],
   selector: 'promote-fault-domain-prompt'
})
export class PromoteDomainComponent implements OnInit {

   clusterRef: ManagedObject;
   domainName: string;

   errors: string[];
   busy: boolean = false;

   readonly dialogTitle: string = VsanUiUtils.getString("vsan.faultDomains.stretchedCluster.action.promote.title");

   constructor(private mutationProvider: VsanStretchedClusterMutationProvider) {}

   ngOnInit() {
      let context: any = VsanUiUtils.getModalContext();
      this.clusterRef = context.clusterRef;
      this.domainName = context.domainName;
   }

   apply = () => {
      let spec = PreferredFaultDomainData.Factory.create(this.domainName);
      this.mutationProvider.setPreferredFaultDomain(this.clusterRef, spec)
            .then(taskRef => VsanUiUtils.closeModalDialog(taskRef ? [taskRef] : []))
            .catch(e => {
               this.errors = [e];
               this.busy = false;
            });

      this.busy = true;
   }
}