import {DomainOrHostData} from "@generated/domain-or-host-data";
import {FaultDomainModel} from "@model/fault-domain.model";
import {VsanUiUtils} from "@util/vsan-util";

/**
 * Holds the data for the MoveHostsConfirmationComponent dialog.
 */
export class MoveHostsContext {

   hosts: DomainOrHostData[];
   objectInfo: string;
   sourceDomain: string;
   /**
    * Whether this operation will result in
    * the source domain being destroyed
    * because all its hosts are going to be removed.
    */
   warnDestructiveForSourceDomain: boolean;

   static createDomain(hosts: FaultDomainModel[]): MoveHostsContext {
      return new MoveHostsContext(
            hosts,
            VsanUiUtils.getString("vsan.faultDomains.action.createDomain.title"),
            VsanUiUtils.getString("vsan.faultDomains.action.createDomain.message.create"),
            null,
            VsanUiUtils.getString("vsan.common.create"),
            "btn-success",
            true);
   }

   static renameDomain(domain: FaultDomainModel): MoveHostsContext {
      return new MoveHostsContext(
            domain.hosts,
            VsanUiUtils.getString("vsan.faultDomains.action.renameDomain"),
            VsanUiUtils.getString("vsan.faultDomains.action.renameDomain.message"),
            null,
            VsanUiUtils.getString("vsan.faultDomains.action.renameDomain.action"),
            "",
            true,
            true);
   }

   static deleteDomain(domain: FaultDomainModel): MoveHostsContext {
      return new MoveHostsContext(
            domain.hosts,
            VsanUiUtils.getString("vsan.faultDomains.action.deleteDomain"),
            VsanUiUtils.getString("vsan.faultDomains.action.deleteDomain.message", domain.data.uid),
            null,
            VsanUiUtils.getString("vsan.common.delete"),
            "btn-danger",
            false,
            true);
   }

   static moveHosts(hosts: FaultDomainModel[], targetDomain: string): MoveHostsContext {
      const operationName: string = VsanUiUtils.getString(targetDomain
            ? "vsan.faultDomains.action.moveHosts.moveIn"
            : "vsan.faultDomains.action.moveHosts.moveOut");
      const message: string = targetDomain
            ? VsanUiUtils.getString("vsan.faultDomains.action.moveHosts.moveIn.message", targetDomain)
            : VsanUiUtils.getString("vsan.faultDomains.action.moveHosts.moveOut.message", hosts[0].domain.data.uid);
      const buttonText: string = VsanUiUtils.getString("vsan.faultDomains.action.moveHosts.action");
      return new MoveHostsContext(hosts, operationName, message, targetDomain, buttonText);
   }

   constructor(
         hostModels: FaultDomainModel[],
         public operation: string,
         public message: string,
         public targetDomain: string,
         public actionButtonLabel: string,
         public actionButtonClass: string = "",
         public promptForDomainName: boolean = false,
         public suppressDestructiveWarn: boolean = false) {
      this.hosts = this.getHosts(hostModels);
      this.objectInfo = this.getObjectInfo(hostModels, this.hosts);
      this.sourceDomain = this.getSourceDomainName(hostModels);
      this.warnDestructiveForSourceDomain = this.shouldWarnDestructiveForSourceDomain(hostModels);
   }

   private getHosts(hostModels: FaultDomainModel[]): DomainOrHostData[] {
      return hostModels.map(model => model.data);
   }

   private getObjectInfo(hostModels: FaultDomainModel[], hosts: DomainOrHostData[]): string {
      if (this.suppressDestructiveWarn) {
         // domain operation
         return hostModels[0].domain.data.uid;
      }
      if (hosts.length === 1) {
         return hosts[0].label;
      }
      return VsanUiUtils.getString("vsan.faultDomains.action.hostCount", hosts.length);
   }

   private getSourceDomainName(hostModels: FaultDomainModel[]): string {
      if (!hostModels[0].domain?.data) {
         return null;
      }
      return hostModels[0].domain.data.uid;
   }

   private shouldWarnDestructiveForSourceDomain(hostModels: FaultDomainModel[]): boolean {
      return !this.suppressDestructiveWarn && FaultDomainModel.allAffected(hostModels);
   }

}
